	module.exports = {"data":{"talkJson":{"description":"\"Speaker: Brett Slatkin\n\nFunctions improve readability, encourage reuse, and facilitate refactoring. Python has many unique features that make functions significantly more powerful. This talk will show you the best ways to use functions in Python: when *args is helpful and when it'll crash your programs; how to use generators for arguments and return values; the value of keyword vs. keyword-only arguments; and more!\n\nSlides can be found at: https://speakerdeck.com/pycon2015 and https://github.com/PyCon/2015-slides\"","meta":{"duration":"PT29M39S"},"presenter":{"name":null},"slug":"brett-slatkin-how-to-be-more-effective-with-functions-pycon-2015","source":{"id":"WjJUPxKB164","provider":"youtube"},"tag":[],"title":"Brett Slatkin - How to Be More Effective with Functions - PyCon 2015"},"allTalkJson":null},"pathContext":{"slug":"brett-slatkin-how-to-be-more-effective-with-functions-pycon-2015","tags":[]}}

